import { Page } from "@components";
import { MainLayout } from "@layouts";
import { useAppDispatch, useAppSelector } from "@store";
import React from "react";
import { deliveryTypeControllers } from "@controllers";
import classNames from "classnames";

export const AddDeliveryType: React.FC = (): JSX.Element => {
  const dispatch = useAppDispatch();
  const { pending } = useAppSelector((state) => state.deliveryType);

  const [name, setName] = React.useState<string>("");
  const [price, setPrice] = React.useState<string>("0");

  const handleChangePrice = React.useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      const value = Number(e.target.value);
      if (isNaN(value)) return;
      if (value < 0) return;
      setPrice(e.target.value);
    },
    []
  );

  const handleCreateDeliveryType = React.useCallback(() => {
    dispatch(deliveryTypeControllers.create({ name, price }));
  }, [name, price, dispatch]);

  return (
    <Page title="  ">
      <MainLayout>
        <div className="add-characteristics">
          <h1 className="title mb-3">  </h1>
          <div className="deliveryType__add-new mb-3">
            <div className="d-flex gap-20">
              <div className="deliveryType__add-new-label">
                <p> </p>
                <input
                  type="text"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  className="deliveryType__add-new-input"
                />
              </div>
              <div className="deliveryType__add-new-label">
                <p></p>
                <input
                  type="string"
                  value={price}
                  onChange={handleChangePrice}
                  className="deliveryType__add-new-input"
                />
              </div>
            </div>
            <button
              onClick={handleCreateDeliveryType}
              disabled={pending}
              className={classNames(
                "deliveryType__add-new-btn",
                pending && "deliveryType__add-new-btn_pending"
              )}
            >
              
            </button>
          </div>
        </div>
      </MainLayout>
    </Page>
  );
};
